.mee{
    position: relative;
    border-radius: 12rem 12rem 0 0;
    width: 400px;
    height: 100;
    left: calc(50% - 11rem);
    margin-top: 50px;
  

}

/* ================ SCROLL DOWN ==================*/

.scroll__down{
    position: absolute;
    right: 9rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem
}

/* ==================== media qureis (media device) ==============*/
@media screen and (max-width: 1024px){
    header{
        height:68vh 
    }
}




/* ================= media qureis (small device) ================*/
@media screen and (max-width: 600px){
    header{
        height: 100vh;
    }
    .header__socials, .scroll__down{
        display: none;
    }
}